
#app {
  width: 100%;
  height: 100%;
}
a:-webkit-any-link {
  text-decoration: none;
}
.view {
  position: fixed;
  top: 0;
  bottom: 0;
  width: 100%;
  padding-top: 40px;
  padding-bottom: 50px;
  overflow: auto;
  -webkit-transition: all 0.4s cubic-bezier(0.55, 0, 0.1, 1);
  transition: all 0.4s cubic-bezier(0.55, 0, 0.1, 1);
}
.forward-enter,
.reverse-leave-active {
  opacity: 0;
  -webkit-transform: translate(50%, 0);
  transform: translate(50%, 0);
}
.forward-leave-active,
.reverse-enter {
  opacity: 0;
  -webkit-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
}
img {
  width: 40px;
  height: 40px;
}



